.modal {
    height: 100vh;
    width: 100%;
    background-color: rgba(0,0,0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.content {
    padding: 30px;
    width: 50vw;
    height: 500px;
    border-radius: 12px;
    background-color: white;
    transform: scale(0.5);
    transition: 0.4s all;
    display: flex;
    align-items: center;
    justify-content: center;
}

.content.active{
    transform: scale(1);
}
